.list {
    display: flex;
    flex-direction: column;
    padding-left: 20px;
    list-style: none;
  }
  
  .item {
    font-size: 14px;
    color: #2a2a2a;
    cursor: pointer;
    width: max-content
  }
  
  .item {
    margin-bottom: 10px;
    color: #000;
    font-size: 20px;
    list-style-type: none;
    text-decoration: none;
  }
  
  .item::before { 
    content: "\1F4FD";
   }
  
   .item:hover {
    text-decoration: underline;
    color: rgba(255,107,10);
    border-radius: 4px;
  }
  